<template>
      <div class="top u-cf">
        <div class="navbar">
          <div class="nav_LOGO">
            <a @click="$router.push({ name: 'home'})">Vá ao café</a>
          </div>
          <ul class="nav_menu">
            <li>
              <a @click="$router.push({ name: 'search'})">搜尋</a>
            </li>
            <li :style="{display:this.like}">
              <a href="#">收藏</a>
            </li>
            <li :style="{display:this.signin}" >
              <a @click="$router.push({ name: 'login'})">登入</a>
            </li>
            <li :style="{display:this.signout}">
              <a  @click="logout">登出</a>
            </li>
          </ul>
        </div>
      </div>
</template>

<script>
import firebase, { functions } from "firebase";
import router from "../router.js";

export default {
    name:"Bar",
    data(){
      return{
        signin:sessionStorage.getItem('signIn'),
        signout:sessionStorage.getItem('signOut'),
        like:sessionStorage.getItem('like')
      }
    },
    methods:{
      async logout() {
      firebase
        .auth()
        .signOut()
        .then(function() {
          firebase.auth().onAuthStateChanged(function(user) {
            if (user) {
              console.log("yes");
            } else {
              console.log("no");
            }
          });
        });
        this.signout="none";
        this.signin="";
        this.like="none";
        sessionStorage.setItem('signIn', signin)
        sessionStorage.setItem('signOut', signout)
        sessionStorage.setItem('like', like)
    }
    }
}
</script>

<style scoped>
@charset "UTF-8";
* {
  margin: auto;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  font-family: "微軟正黑體", cursive;
}

