  color: #fff;
    	background: rgba(255, 255, 255, 0);

}

.u-cf:after {
  content: "";
  display: block;
  clear: both;
}

.AREA {
  width: 100%;
  height: 100%;
  cursor: default;
  background-image: url(../assets/im/Tbg-b.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
  overflow:auto;
}

.wrapper {
  position: absolute;
  width: 100%;
  height: auto;
  cursor: default;
}

.top {
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 15;
}

.navbar {
